import './styles/section4.css'
import { useContext, useLayoutEffect, useRef } from "react";
import {ScrollTrigger} from 'gsap/ScrollTrigger'
import {gsap, Power3} from 'gsap'
import { LangContext } from '../../../context/LangContext';
import { ReactComponent as Twitter } from './images/twitter.svg';
gsap.registerPlugin(ScrollTrigger)
export default function Section4() {
    const section4 = useRef(null)
    const h1 = useRef(null)
    const h3 = useRef(null)
    const descript = useRef(null)
    const more = useRef(null)
    const quote1 = useRef(null)
    const quote2 = useRef(null)
    const quote3 = useRef(null)
    useLayoutEffect(() => {
        gsap.from([h3.current, h1.current], {
            scrollTrigger: {
                trigger: ".section4",
                start: 'center bottom',
            },
            stagger: 0.5,
            duration: 0.5,
            ease: Power3.easeOut(),
            opacity: 0,
            y: -20,
        })
        gsap.from(descript.current, {
            scrollTrigger: {
                trigger: ".section4",
                start: 'center bottom',
            },
            delay: 1,
            duration: 0.5,
            ease: Power3.easeOut(),
            opacity: 0,
            x: -100,
        })
        gsap.from(more.current, {
            scrollTrigger: {
                trigger: ".section4",
                start: 'center bottom',
            },
            delay: 3,
            scale: 0.5,
            duration: 0.3,
            opacity: 0,
            ease: 'linear',
            y: -10,
        })
        gsap.from([quote1.current, quote3.current], {
            scrollTrigger: {
                trigger: ".section4",
                start: 'center bottom',
            },
            stagger: 1,
            delay: 1.5,
            duration: 0.5,
            opacity: 0,
            ease: Power3.easeOut(),
            x: 50,
        })
        gsap.from(quote2.current, {
            scrollTrigger: {
                trigger: ".section4",
                start: 'center bottom',
            },
            delay: 2,
            duration: 0.5,
            opacity: 0,
            ease: Power3.easeOut(),
            x: -50,
        })
        gsap.to(section4.current, {
            scrollTrigger: {
                scrub: 1,
                trigger: ".section4",
                start: 'bottom bottom',
            }, 
            opacity: 0,
            scale: 0.9
        })
    }, [])
    const {lang} = useContext(LangContext)
    return(
        <article ref={section4} className='section4' id='section4'>
            <div className="gradient"></div>
            <div className="content" id='content'>
                <div className="left">
                {!lang && <h3 ref={h3}>What people think?</h3>}
                {lang && <h3 ref={h3} className='ru-h3'>Что думают люди?</h3>}
                {!lang && <h1 ref={h1}>Press Quotes</h1>}
                {lang && <h1 ref={h1} className='ru-h1'>Цитаты прессы</h1>}
                {!lang && <p ref={descript} className='descript'>
                    Our goal is to create a product and service that you’re satisfied with and use it every day. 
                    This is why we’re constantly working on our services 
                    to make it better every day and really listen to what our users has to say.
                </p>}
                {lang && <p ref={descript} className='descript'>
                    Наша цель - создать продукт и услугу, которыми вы будете довольны, и пользоваться ими каждый день. 
                    Вот почему мы постоянно работаем над нашими сервисами,
                    чтобы с каждым днем делать их лучше, и действительно прислушиваемся к тому, что говорят наши 
                    пользователи.
                </p>}
                {!lang && <button ref={more} className="buy">
                    <p>Read more testimonials</p>
                </button>}
                {lang && <button ref={more} className="buy">
                    <p>Читате больше отзывов</p>
                </button>}
                </div>

                <div className="right">
                    <div className="quote" ref={quote1}>
                        <div className="head">
                            <div className="pro">
                                <div className="profile">
                                    <img src={require('./images/profile1.png')} alt="" />
                                </div>
                                <div className="name">
                                    <h2>Evan Lahti </h2>
                                    {!lang && <p className="who">PC Gamer</p>}
                                    {lang && <p className="who">Компьютерный Геймер</p>}
                                </div>
                            </div>
                            <a href='#' className="twitter"><Twitter /></a>
                        </div>
                        {!lang && <q>One of my gaming highlights of the year.</q>}
                        {lang && <q>Одно из моих самых ярких игровых событий этого года.</q>}
                        {!lang && <p className="date">October 18, 2018</p>}
                        {lang && <p className="date">18 Октября, 2018</p>}
                    </div>


                    <div ref={quote2} className="quote">
                        <div className="head">
                        <div className="pro">
                            <div className="profile">
                                <img src={require('./images/profile2.png')} alt="" />
                                </div>
                                <div className="name">
                                    <h2>Jada Griffin </h2>
                                    <p className="who">Nerdreactor</p>
                                </div>
                            </div>
                            <a href='#' className="twitter"><Twitter /></a>
                        </div>
                        {!lang && <q>The next big thing in the world of streaming and survival games.</q>}
                        {lang && <q>Следующая крупная вещь в мире стриминга и игр на выживание.</q>}
                        {!lang && <p className="date">December 21, 2018</p>}
                        {lang && <p className="date">21 Декабря, 2018</p>}
                    </div>



                    <div ref={quote3} className="quote">
                        <div className="head">
                            <div className="pro">
                                <div className="profile">
                                    <img src={require('./images/profile3.png')} alt="" />
                                </div>
                                <div className="name">
                                    <h2>Aaron Williams</h2>
                                    <p className="who">Uproxx</p>
                                </div>
                            </div>
                            <a href='#' className="twitter"><Twitter /></a>
                            
                        </div>
                        {!lang && <q>Snoop Dogg Playing The Wildly Entertaining ‘SOS’ Is Ridiculous.</q>}
                        {lang && <q>Снуп Догг, играющий в безумно увлекательную "SOS", Просто смешон.</q>}
                        {!lang && <p className="date">December 24, 2018</p>}
                        {lang && <p className="date">24 Декабря, 2018</p>}
                    </div>
                </div>

                
            </div>
            
        </article>
    )
}